export const Select = (selector:string, all=false) => {
        
    if(all){
        return document.querySelectorAll(selector);
        
    }else{
      
        return document.querySelector(selector);
    }
    
};

export const toggleNavMenu = (openning=false, value?:string) => {
    let navigationMenu = Select(".navigation-container") as HTMLDivElement;
    
    if (openning) {
        navigationMenu.classList.remove('navigation-slideout')
        navigationMenu.classList.add('navigation-slidein')
        
    } else {
        navigationMenu.classList.remove('navigation-slidein')
        navigationMenu.classList.add('navigation-slideout')
        
    }

    if(value) navigationMenu.style.visibility = value;
}

export const toggleMenu = () => {
    let hamburger = Select("#hamburger") as HTMLInputElement;
    
    if (hamburger) {
        if(matchMediaSize('max-width : 980px').matches){
            toggleNavMenu(false)
        }
        hamburger['checked'] = false;
    }

};

export const toggleHamburgerMenu = (mql:any) => {
    let hamburger = Select(".hamburger-box") as HTMLDivElement;
                           
    if (mql && mql.matches) {
        hamburger.style.visibility = 'visible';
        console.log(hamburger)    
        toggleNavMenu(false);

    }else{
        hamburger.style.visibility = 'hidden';
        console.log(hamburger)    
        toggleNavMenu(true, 'visible');
    }
    
};

export const handleHambugerClick = () =>{
    let hamburger = Select("#hamburger") as HTMLInputElement;
             
    if (hamburger.checked) {
        toggleNavMenu(true);
       
    }else{
        toggleNavMenu(false);
    }
}


export const getSectionClassName = (viewPath:string):string => {
    switch (viewPath) {
        case '/home/':
            return '.app-container';

        case '/about/':
            return '.about-container';

        case '/resume/':
            return '.resume-container';

        case '/contacts/':
            return '.contacts-container';

        case '/skills/':
            return '.skills-container';

        case '/projects/':
                                
            return '.projects-container';
    
        default:
            return '';
    }
}

export const matchMediaSize = (mediaSize:string):MediaQueryList =>{
    let size = `(${mediaSize})`;
    return window.matchMedia(size);
};

export const handlePageScroll =()=>{
    let bacTopBtn = Select('.back-to-top') as HTMLButtonElement;
    console.log(window.scrollY)
    console.log(window.scrollY !== 0)
    if (window.scrollY !== 0) {
        bacTopBtn.style.visibility = 'visible';
        console.log(bacTopBtn)
       
    }else {
        bacTopBtn.style.visibility = 'hidden';
    }
    
    let navButtons = Select('.navbar-item', true) as NodeListOf<HTMLElement>;
    let position = window.scrollY + 250;
    
    navButtons.forEach(element => {

        let hash = element.getAttribute('href') || '';
        let section = Select(hash) as HTMLDivElement;

        element = element.previousSibling as any;
        let svg = element as Element;
        if (section) {
            if (position >= section.offsetTop && 
                position <= (section.offsetTop + section.offsetHeight)) {
                svg.classList.add('active')  

            }else{
                svg.classList.remove('active')   
            }
        }
        
    });
};   

export const _scrollTo = (url='') => {
	toggleMenu()
				 
	let className = getSectionClassName(url)
	if(className){
		const element = Select(className) as HTMLDivElement;
		element.scrollIntoView({behavior: "smooth", block: "start", inline: "nearest"});
	}
}

export const scrollToView = (ev:any) => {
	ev.preventDefault()
	let target = ev?.target;
		
	if (target) {
		const navigationData = target.dataset.value;
		if(navigationData) _scrollTo(navigationData);
	}
}