const filtersDefault = [
  { label: "all", isChecked: true, name:'All' },
  { label: "filter-app", isChecked: false, name:'App' },
  { label: "filter-card", isChecked: false, name:'Card'},
  { label: "filter-web", isChecked: false, name:'Web'},
  
];

const modes = ["out-in", "in-out"];


const Projects:React.FC  = () => {
    const [filters, updateFilters] = React.useState(filtersDefault);
    const [mode, setMode] = React.useState("out-in");
  const [state, setState] = React.useState(true);

	
	const onFilter = (event: { target: { value: any; checked: any; }; }) => {
		const {
		  target: { value, checked }
		} = event;
	
		updateFilters(state =>
		  state.map(f => {
			if (f.label === value) {
			  return {
				...f,
				isChecked: checked
			  };
			}
	
			return f;
		  })
		);
	};
    
        
    return(
        <section id="projects-container" className="projects-container section-bg">
			<div className="container">
				<div className="section-title">
					<h2>Portfolio</h2>
					<p>portfolio section.</p>
          		</div>
  
          		<div className="row" data-aos="fade-up">
            		<div className="col-lg-12 d-flex justify-content-center">
              			<ul id="portfolio-flters">
                			
							{filters.map((f:any) => (
								<Filter key={`${f.label}_key`}>
									<input
									    id={f.label}
										type="checkbox"
										value={f.name}
										onChange={onFilter}
										checked={f.isChecked}
								    />
									<label htmlFor={f.label}>{f.name}</label>
								</Filter>
							))}
              			</ul>
            		</div>
          		</div>
               <PortfolioCards></PortfolioCards> 
    		</div>
      </section>
    )

}

export default Projects; 


