import { useEffect } from 'react';

import AOS from 'aos';
import Typed from 'typed.js';
import PureCounter from "@srexi/purecounterjs";

import MainView from './views/main-view';
import { handleHambugerClick, 
		handlePageScroll,
		scrollToView, 
		Select,
		toggleHamburgerMenu, _scrollTo } from './utils';
import { NavigationBar } from './views/navigation-bars';
import { ArrowUp } from 'react-feather';
import { TypeWriter } from './type-writer';


function App() {
    
	const backTotop = () => {
		window.scrollTo(0, 0);
	}
	
	useEffect(()=>{
		AOS.init({
			duration: 1000,
			easing: 'ease-in-out',
			once: true,
			mirror: false
		})
		
		document.addEventListener('scroll', handlePageScroll);
		const navButtons = Select('.navigation-box') as HTMLButtonElement; 
		navButtons?.addEventListener('click', scrollToView)

		let mql = window.matchMedia("(max-width : 980px)");
        mql.onchange = () => {
            toggleHamburgerMenu(mql)
        }
                            
        toggleHamburgerMenu(mql)  
		_scrollTo()

		const backTotopBtn = Select('.back-to-top') as HTMLButtonElement;
		
		let navBarTrigger = Select("#hamburger") as HTMLLabelElement;
		console.log(navBarTrigger)
		navBarTrigger?.addEventListener('click', handleHambugerClick)
		backTotopBtn?.addEventListener('click', backTotop)

		TypeWriter()
					  
		new PureCounter();

		return ()=> {
			document.removeEventListener('scroll', handlePageScroll); 
            navButtons.removeEventListener('click', scrollToView);
			navBarTrigger.removeEventListener('click', handleHambugerClick);
			backTotopBtn.removeEventListener('click', backTotop);
		}
	})

    return (
		<div id="app-container" className="container-fluid app-container">
            <NavigationBar/>
            
			<div className="hamburger-box" data-aos="fade-left" >
                <input type="checkbox" id="hamburger" hidden={true} name="hamburger" />
                <label htmlFor="hamburger" className="navbar-trigger">
                    <span></span>
                    <span></span>
                    <span></span>
                </label>
            </div>

            <MainView/>

			<button type="button" className="btn-sm back-to-top" data-aos="fade-left">
                <ArrowUp size="25"/>
            </button>
        </div>
    );
};

export default App;